<template>
<ul v-if="authenticated" class="nav navbar-nav pull-xs-right">
  <li class="nav-item notification-count">
    <a style="color:#FFF;" class="nav-link" v-bind:class="{ 'new': notificationCount }">
      <i class="fa fa-bell-o"></i>
      <span class="count">
        {{ notificationCount }}
      </span>
    </a>
  </li>
  <li class="nav-item">
    <a style="color:#FFF;" v-link="{ path: '/shop' }" class="nav-link">
      <i class="fa fa-home"></i>
    </a>
  </li>
  <li class="nav-item dropdown" @click="viewUserDropdown">
    <span class="nav-link" style="color:#FFF;">
      <i class="fa fa-reorder"></i>
    </span>
    <div class="dropdown-menu dropdown-menu-right" v-show="showUserDropdown">
      <a class="dropdown-item" v-link="{ path: '/profile'}"> </a>
      <div class="dropdown-divider"></div>
      <a class="dropdown-item" @click="logout"> </a>
    </div>
  </li>
</ul>
<ul v-else class="nav navbar-nav pull-xs-right">
  <li class="nav-item">
    <a v-link="{ path: '/signin' }" class="btn btn-sm btn-sign-in-outline"></a>
  </li>
</ul>
</template>
<script>
  export default {
    props: ['authenticated'],
    data () {
      return {
        showUserDropdown: false,
        notificationCount: 0
      }
    },
    methods: {
      viewUserDropdown (e) {
        e && e.preventDefault()
        this.showUserDropdown = !this.showUserDropdown
      },
      logout (e) {
        e && e.preventDefault()

        localStorage.token = ''
        localStorage.mobile = ''

        this.$dispatch('logout')
      }
    }
  }
</script>



