<template>
  <div id="main">
    <div class="header">
      <nav class="navbar navbar-light bg-faded" style="background-color:#3775a8;">
        <div class="container">
          <a v-link="'welcome'">
            <img src="./assets/logo.png" style="margin-top:-5px;" />
          </a>

          <user-bar :authenticated="authenticated"></user-bar>
        </div>
      </nav>
    </div>
    <div class="container" style="padding-top:50px;">
      <div class="row">
        <left-bar v-if="authenticated"></left-bar>
        <div v-bind:class="[authenticated ? 'col-sm-9' : 'col-sm-12']">
          <div :class="rightContent">
            <router-view></router-view>
          </div>
        </div>
      </div>
    </div>

    <footer class="bd-footer text-muted">
      <div class="container">
        Copyright 2013 &copy; - <a href="#"></a> - <a href="http://www.miibeian.gov.cn/">ICP15005696-3</a>
      </div>
    </footer>
    <div class="modal-mask" v-show="showAlert" transition="modal">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header">
            <span>{{error.title}}</span>
            <i class="fa fa-close pull-xs-right" @click="show = false"></i>
          </div>
          <div class="modal-body">
            {{error.msg}}
          </div>
          <div class="modal-footer">
            <a @click="showAlert = false" class="weui_btn_dialog primary"></a>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    data () {
      return {
        user: {
          mobile: localStorage.mobile,
          token: localStorage.token
        },
        authenticated: false,
        showAlert: false,
        error: {
          title: '',
          msg: '',
          status: 200,
          statusText: ''
        }
      }
    },
    computed: {
      rightContent () {
        return this.authenticated ? 'card card-block right-content' : ''
      }
    },
    methods: {
      check () {
        if (!localStorage.mobile || localStorage.mobile === null || !localStorage.token || localStorage.token === null) {
          this.user.mobile = ''
          this.user.token = ''
          return false
        }
        this.user.mobile = localStorage.mobile
        this.user.token = localStorage.token
        return true
      }
    },
    ready () {
      this.authenticated = this.check()
    },
    events: {
      'response-msg' (response) {
        this.showAlert = true
        this.error.status = response.status
        this.error.statusText = response.statusText
        if (response.status === 401) {
          this.$route.router.go('signin')
          localStorage.mobile = ''
          localStorage.token = ''
          this.error.title = response.statusText
          this.error.msg = response.data.error
        } else if (response.status === 0) {
          this.showAlert = true
          this.error.title = ''
          this.error.msg = ''
        } else {
          this.showAlert = true
          this.error.title = response.statusText
          this.error.msg = response.data.error
        }
      },
      'authenticated' () {
        this.authenticated = true
        this.$route.router.go('shop')
      },
      'logout' () {
        this.authenticated = false
        this.$route.router.go('signin')
      }
    },
    components: {
      'left-bar': require('./components/left-bar.vue'),
      'user-bar': require('./components/user-bar.vue')
    }
  }
</script>



